.events-add {
    padding: 20px 0 12px;

    &__image {
        height: 188px;
        width: 100%;
        background: var(--minor);
        border-radius: 10px;
        margin-bottom: 16px;
        display: flex;
        justify-content: center;
        align-items: center;

        &__icon {
            width: 40px;
            height: 40px;
            margin-right: 20px;
            background: url('../../../assets//icons/gallery-add-large.svg') no-repeat center;
        }

        &__content {}

        &__title {
            font-size: 13px;
            font-weight: 500;
            line-height: 16px;
            letter-spacing: -0.08px;
            margin-bottom: 5px;
        }

        &__text {
            color: var(--gray);
            font-size: 13px;
            font-weight: 400;
            line-height: 16px;
            letter-spacing: -0.08px;
        }
    }

    &__inputs {
        display: flex;

        >div {
            margin-right: 8px;
            flex: 1 0 calc(50% - 8px)
        }
    }

    &__divider {
        height: 8px;
        background: var(--gray2);
        margin: 0 -16px;
    }

    &__section {
        padding: 16px 0;
    }

    &__button {
        padding: 8px 16px 12px;
        margin: 0 -16px;
        box-shadow: 0px -4px 24px 0px rgba(0, 0, 0, 0.08);
    }
}