.account-order-id {
    &__content {
        border-bottom: 1px solid var(--minor);
        padding-bottom: 16px;
    }

    &__item {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__date {
        font-size: 15px;
        font-weight: 600;
        line-height: 20px;
        letter-spacing: -0.2px;
    }

    &__status {
        color: var(--white);
        font-size: 12px;
        font-weight: 400;
        line-height: 16px;
        padding: 0 8px;
        line-height: 22px;
        border-radius: 30px;
        background: #f6ad3f;
        margin-bottom: 2px;
    }

    &__number {
        font-size: 15px;
        font-weight: 400;
        line-height: 20px;
        letter-spacing: -0.2px;
        text-decoration: underline;
        margin-bottom: 12px;
    }


    &__address-title {
        color: var(--gray);
        font-size: 12px;
        font-weight: 400;
        line-height: 16px;
        margin-bottom: 3px;
    }

    &__address {
        font-size: 13px;
        font-weight: 400;
        line-height: 16px;
        letter-spacing: -0.08px;
        margin-bottom: 8px;
    }

    &__track {
        color: var(--gray);
        font-family: Inter;
        font-size: 12px;
        font-style: normal;
        font-weight: 400;
        line-height: 16px;
        margin-bottom: 3px;
    }

    &__track-number {
        font-size: 13px;
        font-weight: 400;
        line-height: 16px;
        letter-spacing: -0.08px;
    }

    &__image {
        width: 76px;
        height: 76px;
        border-radius: 4px;
        border: 1px solid var(--minor);
        margin-right: 4px;
        margin-bottom: 4px;
    }

    &__recipient {
        font-size: 15px;
        font-weight: 400;
        line-height: 20px;
        letter-spacing: -0.2px;
    }

    &__list {
        background: var(--gray2);
        padding-top: 4px;
        margin: 0 -16px;

        &__inner {
            padding: 0 16px;
            background: var(--white);
        }
    }

    &__buttons {
        display: flex;
        align-items: center;
        border-top: 1px solid var(--minor);
        padding-top: 16px;
    }
}