.account-product-add {
    padding: 4px 0 16px;

    &__photo {
        width: 80px;
        height: 80px;
        cursor: pointer;
        border-radius: 10px;
        background: var(--minor) url('../../../assets//icons/gallery-add.svg') no-repeat center;
        margin: 8px 0 16px
    }

    &__section {
        padding-bottom: 8px;
        margin: 0 -16px;
        background: var(--gray2);

        &__inner {
            padding: 16px 16px 16px;
            background: var(--white);
        }
    }

    &__inputs {
        display: flex;
        justify-content: space-between;

        >div {
            flex: 1 0 calc(50% - 8px);
            padding-bottom: 16px;

            &:first-child {
                margin-right: 8px;
            }
        }
    }

    &__options {
        display: flex;
    }

    &__accordion {
        background: var(--minor);
        padding: 12px;
        border-radius: 8px;
        margin-bottom: 16px;
    }
}