.account-brands-create {
    padding-top: 24px;

    &__input {}

    &__logo {
        display: flex;
        align-items: center;
        margin: 16px 0;

        &__content {
            &__title {
                font-size: 13px;
                font-weight: 500;
                line-height: 16px;
                letter-spacing: -0.08px;
                margin-bottom: 5px;
            }

            &__text {
                color: var(--gray);
                font-size: 13px;
                font-weight: 400;
                line-height: 16px;
                letter-spacing: -0.08px;
            }
        }
    }

    &__image {
        width: 80px;
        height: 80px;
        justify-content: center;
        align-items: center;
        display: flex;
        background: var(--minor);
        border-radius: 10px;
        margin-right: 16px;
    }
}