.add-options {
    display: flex;
    align-items: center;

    .add-option {
        margin-right: 8px;
    }
}

.add-option {
    height: 44px;
    background: var(--minor);
    border-radius: 6px;
    padding: 12px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex: 1;

    input {
        border: 0;
        background: transparent;
        font-size: 15px;
        line-height: 20px;
        max-width: 50%;
        flex: 1 0 50%;

    }

    &__input-value {
        flex: 1 0 50%;
        max-width: 50%;
        color: var(--gray);
    }

    &__wrapper {
        max-width: calc(60% - 104px);
    }

    &__label {
        margin-bottom: 4px;
    }
}

.add-option-switch {
    display: flex;
    padding: 4px;
    align-self: flex-end;
    background: var(--minor);
    border-radius: 9px;
    flex: 1 0 44px;
    max-width: 88px;

    &__button {
        width: 40px;
        height: 36px;
        border-radius: 8px;

        &_active {
            background: var(--dark);
            color: var(--white)
        }
    }
}