.event {
    padding: 12px;
    border-radius: 12px;
    background: var(--white);
    box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.08);
    display: flex;
    margin-bottom: 8px;
    cursor: pointer;

    &__date {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        margin-right: 20px;
        width: 44px;
        flex: 1 0 44px;

        &__day {
            color: var(--red);
            font-size: 12px;
            font-weight: 400;
            line-height: 16px;
        }

        &__number {
            font-size: 34px;
            line-height: 40px;
            letter-spacing: 0.4px;
        }

        &__month {
            font-size: 12px;
            line-height: 16px;
        }
    }

    &__content {
        display: flex;
        flex-direction: column;
        flex: 1 0 calc(100% - 44px);

        &__label {
            color: var(--gray);
            font-size: 12px;
            font-weight: 400;
            line-height: 16px;
        }

        &__text {
            font-size: 15px;
            font-weight: 500;
            line-height: 20px;
            letter-spacing: -0.2px;
            margin: 4px 0;
        }

        &__address {
            color: var(--gray);
            font-size: 12px;
            font-weight: 400;
            line-height: 16px;
        }
    }
}