.cart {
    padding: 16px 0;

    &__departure {
        margin: 16px 0;
    }

    &__header {
        background: #f5f5f5;
        border-radius: 8px;
        padding: 12px 12px 4px;
        display: flex;
        flex-direction: column;

        &__item {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 6px;
        }

        &__title {
            font-size: 15px;
            font-weight: 500;
            line-height: 20px;
            letter-spacing: -0.2px;
        }

        &__arrow {
            transform: rotate(-90deg);
        }

        &__price {
            font-size: 13px;
            font-weight: 400;
            line-height: 16px;
            letter-spacing: -0.08px;
        }

        &__count {
            color: var(--gray);
            font-size: 13px;
            font-weight: 400;
            line-height: 16px;
            letter-spacing: -0.08px;
        }
    }

    &__list {
        &__item {
            cursor: pointer;
            padding: 16px 0;
            border-bottom: 1px solid #f5f5f5;
            display: flex;

            &:last-child {
                border: 0;
            }


            &__image {
                display: block;
                width: 76px;
                height: 76px;
                border-radius: 4px;
                flex: 1 0 76px;
                border: 1px solid var(--minor);
            }

            &__content {
                padding-left: 10px;
                flex: 1 0 calc(100% - 76px);

                &__item {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                }
            }

            &__title {
                font-size: 13px;
                font-weight: 400;
                line-height: 16px;
                letter-spacing: -0.08px;
                margin-bottom: 4px;

            }

            &__label {
                color: var(--gray);
                font-size: 13px;
                font-weight: 400;
                line-height: 16px;
                letter-spacing: -0.08px;
                margin-bottom: 8px;
            }

            &__price {
                font-size: 15px;
                font-weight: 500;
                line-height: 20px;
                letter-spacing: -0.2px;
                margin-bottom: 8px;
            }
        }
    }
}

.cart-total {
    padding-top: 8px;
    margin: 0 -16px;
    background: #e9e9e9;

    &__inner {
        background: var(--white);
        padding: 16px 16px 32px;
    }

    &__title {
        font-size: 17px;
        font-weight: 500;
        line-height: 24px;
        letter-spacing: -0.4px;
        margin-bottom: 16px;
    }

    &__item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 8px;
    }

    &__count {
        color: var(--gray);
        font-size: 13px;
        font-weight: 400;
        line-height: 16px;
        letter-spacing: -0.08px;
    }

    &__price-1 {
        font-size: 13px;
        font-style: normal;
        font-weight: 400;
        line-height: 16px;
        letter-spacing: -0.08px;
    }

    &__price-2 {
        font-size: 17px;
        font-weight: 600;
        line-height: 24px;
        letter-spacing: -0.4px;
    }

    &__result {
        font-size: 13px;
        font-weight: 400;
        line-height: 16px;
        letter-spacing: -0.08px;
    }

    &__button {
        padding-top: 8px;
    }
}


.count {
    display: flex;
    align-items: center;
    width: 100px;
    justify-content: space-between;

    &__button {
        background: #f5f5f5;
        width: 32px;
        height: 32px;
        border-radius: 6px;

        &:active {
            background: #d1d1d1;
        }
    }

    &__result {
        font-size: 13px;
        font-weight: 400;
        line-height: 16px;
        letter-spacing: -0.08px;

    }
}