.catalog-brands {
    padding: 16px 0;

    &__cards {
        display: flex;
        flex-wrap: wrap;
        padding: 8px 0;
    }

    &__card {
        border-radius: 8px;
        border: 1px solid var(--Grey-2, #E9E9E9);
        padding: 8px;
        display: flex;
        flex-direction: column;
        align-items: center;
        flex: 1 0 calc(50% - 4px);
        width: 100%;
        max-width: calc(50% - 4px);
        height: 128px;
        margin-bottom: 4px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        transition: 200ms;

        &:nth-child(2n) {
            margin-left: 4px;
        }

        &__image {
            max-height: 88px;
        }

        &__title {
            font-size: 12px;
            line-height: 16px;
            color: var(--gray);
        }
    }

}