.order {
    padding: 16px 0;

    &__section {
        padding-bottom: 16px;
    }

    &__types {
        display: flex;
        background: #f5f5f5;
        border-radius: 8px;
    }

    &__type {
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 9px;
        border-radius: 8px;
        cursor: pointer;
        transition: 200ms;

        &:hover {
            background: #e6e6e6;
        }

        &__title {
            margin-bottom: 6px;
            font-size: 13px;
            font-weight: 500;
            line-height: 16px;
            letter-spacing: -0.08px;

        }

        &__label {
            font-size: 11px;
            font-weight: 400;
            line-height: 12px;
            letter-spacing: 0.04px;

        }

        &-active {
            background: #2c2c2c;

            .order__type__title,
            .order__type__label {
                color: #fff;
            }


            &:hover {
                background: #212121;
            }
        }
    }


    &__item {
        padding-bottom: 4px;
        border-bottom: 1px solid #f5f5f5;

        &:last-child {
            border: 0;
        }

        &__header {
            display: flex;
            align-items: center;
            padding-top: 8px;
            margin-bottom: 12px;

            &__icon {
                margin-right: 10px;
            }

            &__title {
                font-size: 15px;
                font-weight: 500;
                line-height: 20px;
                letter-spacing: -0.2px;

            }
        }

        //////
        &__select {
            border-radius: 6px;
            border: 1px solid #a4a4a4;
            height: 44px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 0 8px 0 12px;
            margin-bottom: 12px;

            &__arrow {
                transform: rotate(90deg);
            }

            &-selected {
                justify-content: flex-start;
                border-color: #181818;
            }

            &__icon {
                margin: 0 10px -4px 30px;
                position: relative;

                &:before {
                    content: '';
                    width: 20px;
                    height: 20px;
                    border-radius: 50%;
                    border: 1px solid #181818;
                    display: block;
                    left: -30px;
                    position: absolute;
                }

                &:after {
                    content: '';
                    width: 14px;
                    height: 14px;
                    border-radius: 50%;
                    background: #181818;
                    display: block;
                    position: absolute;
                    left: -26px;
                    top: 4px
                }

                &-disable {

                    &:after {
                        border-color: var(--gray);
                        border-color: red;
                        content: none;
                    }
                }
            }

            &__text {
                font-size: 15px;
                font-weight: 400;
                line-height: 20px;
                letter-spacing: -0.2px;

            }
        }

        &__recipient {
            font-size: 15px;
            font-weight: 400;
            line-height: 20px;
            letter-spacing: -0.2px;
            margin-bottom: 16px;
        }

        &__comment {
            font-size: 13px;
            font-weight: 400;
            line-height: 16px;
            letter-spacing: -0.08px;
            margin-bottom: 4px;
        }

        &__input {
            color: var(--gray);
            font-size: 15px;
            font-weight: 400;
            line-height: 20px;
            letter-spacing: -0.2px;
            height: 44px;
            border-radius: 6px;
            border: 1px solid var(--gray);
            width: 100%;
            padding: 0 12px;
        }
    }
}

.cart-card {
    padding-bottom: 8px;
    background: #e9e9e9;
    margin: 0 -16px;

    &__inner {
        background: #fff;
        padding: 12px 16px 16px 16px;
    }

    &__header {
        padding: 4px 0px;
        margin-bottom: 8px;
    }

    &__title {
        font-size: 17px;
        font-weight: 500;
        line-height: 24px;
        letter-spacing: -0.4px;
    }

    &__images {
        display: flex;
        margin-bottom: 8px;
    }

    &__image {
        width: 76px;
        height: 76px;
        border-radius: 4px;
        border: 1px solid var(--minor);
        margin-right: 4px;
    }

    &__item {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__count {
        font-size: 13px;
        font-weight: 400;
        line-height: 16px;
        letter-spacing: -0.08px;
        text-decoration: underline;
    }

    &__price {
        font-size: 15px;
        font-style: normal;
        font-weight: 600;
        line-height: 20px;
        letter-spacing: -0.2px;

    }
}