.catalog-brand {
    // padding: 16px 0;
    margin: 0 -16px;

    &__header {
        padding: 4px 16px 8px;

        &__inner {
            display: flex;
            align-items: center;
            margin-bottom: 8px;
        }
    }

    &__image {
        width: 80px;
        height: 80px;
        border-radius: 8px;
        border: 1px solid #f5f5f5;
        margin-right: 12px;
    }

    &__title {
        font-weight: 500;
        font-size: 17px;
        line-height: 24px;
    }
}