.account-order {
    &__support {
        padding: 16px 0;

        &__text {
            font-weight: 500;
            margin-bottom: 16px;
        }
    }

    &__tabs {
        padding: 12px 0 16px;

        button {
            margin-right: 4px;
        }
    }

    &__list {
        margin: 0 -16px;
        display: flex;
        flex-direction: column;
    }
}


.account-order-card {
    background: #e6e6e6;
    padding-top: 4px;

    &__content {
        background: var(--white);
        padding: 16px 16px 12px 16px;
    }

    &__item {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__date {
        font-size: 15px;
        font-weight: 600;
        line-height: 20px;
        letter-spacing: -0.2px;
    }

    &__status {
        color: var(--white);
        font-size: 12px;
        font-weight: 400;
        line-height: 16px;
        padding: 0 8px;
        line-height: 22px;
        border-radius: 30px;
        background: #55c221;
        margin-bottom: 2px;
    }

    &__number {
        font-size: 15px;
        font-weight: 400;
        line-height: 20px;
        letter-spacing: -0.2px;
        text-decoration: underline;
        margin-bottom: 12px;
    }

    &__price {
        font-size: 15px;
        font-weight: 500;
        line-height: 20px;
        letter-spacing: -0.2px;
        margin-bottom: 12px;
    }

    &__address-title {
        color: var(--gray);
        font-size: 12px;
        font-weight: 400;
        line-height: 16px;
        margin-bottom: 3px;
    }

    &__address {
        font-size: 13px;
        font-weight: 400;
        line-height: 16px;
        letter-spacing: -0.08px;
        margin-bottom: 8px;
    }

    &__track {
        color: var(--gray);
        font-family: Inter;
        font-size: 12px;
        font-style: normal;
        font-weight: 400;
        line-height: 16px;
        margin-bottom: 3px;
    }

    &__track-number {
        font-size: 13px;
        font-weight: 400;
        line-height: 16px;
        letter-spacing: -0.08px;
    }

    &__image {
        width: 76px;
        height: 76px;
        border-radius: 4px;
        border: 1px solid var(--minor);
        margin-right: 4px;
        margin-bottom: 4px;
    }
}