.events-id {
    &__header {
        margin-bottom: 12px;
        display: flex;
        justify-content: space-between;
        align-items: center;

        &__title {
            flex: 1;
            padding: 0 10px;
        }

        &__brand {
            width: 40px;
            height: 40px;
            border: 1px solid var(--minor);
            border-radius: 8px;
        }
    }

    &__image {
        margin-bottom: 12px;
    }

    &__title {
        font-size: 17px;
        font-weight: 500;
        line-height: 24px;
        letter-spacing: -0.4px;
        margin-bottom: 16px;
    }

    &__info {
        padding-bottom: 4px;

        &__item {
            display: flex;
            margin-bottom: 12px;

            &__icon {
                margin-right: 8px;

            }

            &__text {}
        }
    }

    &__result {
        padding: 12px;
        border-radius: 6px;
        background: var(--minor);
        margin-bottom: 16px;
    }

    &__content {
        ul {
            padding-left: 6px;

            li {
                display: flex;
                align-items: center;

                &:before {
                    content: '';
                    display: inline-block;
                    width: 4px;
                    height: 4px;
                    border-radius: 50%;
                    background: var(--dark);
                    margin-right: 8px;
                }
            }
        }
    }
}