.account-brands {
    padding-top: 16px;

    &__card {
        display: flex;
        border-radius: 8px;
        background: var(--white);
        box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.08);
        margin-bottom: 8px;
        padding: 8px;
        align-items: center;

        &__image {
            width: 60px;
            height: 60px;
            border-radius: 8px;
            border: 1px solid var(--minor);
            margin-right: 12px;
        }

        &__title {
            font-size: 15px;
            font-weight: 500;
            line-height: 20px;
            letter-spacing: -0.2px;
            margin-bottom: 4px;
        }

        &__text {
            color: var(--gray);
            font-size: 13px;
            font-weight: 400;
            line-height: 16px;
            letter-spacing: -0.08px;
        }
    }
}