.account {
    &__header {
        display: flex;
        flex-direction: column;
        padding-bottom: 16px;

        &__item {
            margin-top: 8px;
        }

        &__label {
            font-size: 12px;
            line-height: 16px;
            padding: 0 8px;
            line-height: 22px;
            border: 1px solid var(--minor);
            border-radius: 50px;
            align-self: flex-start;
            margin-top: 8px;
        }
    }

    &__menu {
        background: var(--gray2);
        margin: 0 -16px;
        padding-top: 8px;

        &__item {
            background: var(--white);
            padding: 12px 16px;
        }

        &__nav {
            display: flex;
            flex-direction: column;

            &__item {
                padding: 8px 0;
                display: flex;
                align-items: center;
                &-logout {
                    margin-top: 28px;
                    .account__menu__nav__title {
                        color: var(--gray);
                    }
                }
            }
            &__icon {
                height: 24px;
                margin-right: 12px;
            }
    
            &__title {
                font-size: 15px;
                line-height: 20px;
                font-weight: 500;
            }
        }
    }
}