.support {
    margin: 0 -16px;
    flex-direction: column;
    display: flex;
    height: calc(100vh - 52px);

    &__dialog {
        flex: 1;
        background: #fff url('../../assets/images/chat-layout.png') no-repeat;
        background-size: 100%;
    }

    &__text-input {
        height: 60px;
        width: 100%;
        border-top: 1px solid var(--minor);
        display: flex;
        align-items: center;
        padding: 0 8px 0 12px;
        background: var(--white);
        max-width: 100%;
    }

    &__input {
        background: transparent;
        font-size: 15px;
        line-height: 20px;
        letter-spacing: -0.2px;
        flex: 1;
        border: 0;

        &::placeholder {
            color: var(--gray1);
        }

        &__send {
            width: 40px;
            height: 40px;
            border-radius: 50%;
            background: #6c6c6c;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
}